import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import PCNavBar from './PCNavBar';
import AddUserComponent from './PCAddUserComponent';
import PCBoardList from './PCBoardList';
import UserTable from './UserTable';
import PCCreateBoard from './PCCreateBoard';


const AppRouter = () => {
    return(
        <div>
            <BrowserRouter>
                <div style={style}>
                    <switch>
                        <Route exact path="/users" component={PCNavBar}/>
                        <Route path = "/add-user" component={AddUserComponent}/>
                        <Route path = "/board" component={PCBoardList}/>
                        <Route path = "/testboardList" component={UserTable}/>
                        <Route path = "/createboard" component={PCCreateBoard}/>
                        <Route exact path="/" component={PCNavBar}/>
                    </switch>
                </div>
            </BrowserRouter>
        </div>
    );
}

const style = {
    marginTop: '20px'
}

export default AppRouter;