import React, { Component } from 'react';
import NavBar from './PCNavBar';
import Container from '@material-ui/core/Container';
import faker from "faker/locale/ko";
import { Link as RouterLink } from 'react-router-dom';
import Link from '@material-ui/core/Link';
import ApiService from '../ApiService';
import {
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableFooter,
    TableHead,
    TablePagination,
    TableRow,
    Paper
} from "@material-ui/core";

faker.seed(123);

const users = Array(53)
    .fill()
    .map(() => ({
        id: faker.random.uuid(),
        name: faker.name.lastName() + faker.name.firstName(),
        email: faker.internet.email(),
        phone: faker.phone.phoneNumber()
    }));



class PCBoardList extends Component {

    constructor(props) {
        super(props);

        this.state = {

            boards: [],
            page: 0,
            rowsPerPage: 10,
        }
    }

    componentDidMount() {
        this.reloadBoardList();
    }

    reloadBoardList = () => {
        ApiService.fetchBoards()
            .then(res => {
                this.setState({
                    boards: res.data
                })
            })
            .catch(err => {
                console.log('reloadBoardList() 에러', err);
            })
    }

    handleChangePage = (event, newPage) => {
        this.setState({
            page: newPage
        })
    }

    handleChangeRowsPerPage = (event) => {
        this.setState({
            rowsPerPage: event.target.value,
            page: 0
        })
    }

    testBoard(no) {
        this.props.history.push('/board/' + no);
    }

    createBoard = () => {

        window.localStorage.removeItem("boardID");
        this.props.history.push('/createboard');
    }

    render() {
        return (
            <div>
                <Container>
                    <NavBar />
                </Container>
                <TableContainer component={Paper}>
                    <div className="cdiv">
                        <button type="button" className="btn btn-sm btn-primary" id="btnWrite" onClick={this.createBoard}>글쓰기</button>
                        <Table size="small">
                            <TableHead>
                                <TableRow>
                                    <TableCell>No</TableCell>
                                    <TableCell align="right">제목</TableCell>
                                    <TableCell align="right">닉네임</TableCell>
                                    <TableCell align="right">작성일</TableCell>
                                </TableRow>
                            </TableHead>

                            <TableBody>
                                {this.state.boards.map(board =>
                                <TableRow key={board.id}>
                                    <TableCell component="th" scope="row">
                                        {board.bid}
                                    </TableCell>
                                    <TableCell align="right">
                                        <a onClick={() => this.testBoard(board.bid)}>{board.title}</a>
                                    </TableCell>
                                    <TableCell align="right">{board.nickname}</TableCell>
                                    <TableCell align="right">{board.createdate}</TableCell>
                                </TableRow>
                                    )}
                            </TableBody>
                            <TableFooter>
                                <TableRow>
                                    <TablePagination
                                        count={this.state.boards.length}
                                        page={this.state.page}
                                        rowsPerPage={this.state.rowsPerPage}
                                        onChangePage={this.handleChangePage}
                                        onChangeRowsPerPage={this.handleChangeRowsPerPage}
                                    />
                                </TableRow>
                            </TableFooter>
                        </Table>
                    </div>
                </TableContainer>
            </div>
        );

    }

}


export default PCBoardList